// api.js

import qs from 'qs';

const baseURL =
  process.env.NODE_ENV === 'development'
    ? 'http://localhost:9001'
    : process.env.type === 'test'
    ? 'http://localhost:9001'
    : 'http://localhost:9001';

//  token
const globalData = {
  token: '',
};


// 
uni.addInterceptor('request', {
  // 
  invoke(request) {
    //  token 
    if (globalData.token) {
		console.log("token");
      request.header['Authorization'] = `Bearer ${globalData.token}`;
    } else {
      console.error(' token');
      // 
      // 
      // ...
	  // uni.navigateTo({
	  // 	url:"/pages/tabbar/tabbar-2/tabbar-2"
	  // })
    }
  },
});


function request(config) {
  return new Promise((resolve, reject) => {
	const handleRequest  = () => {
		uni.request({
		  url: config.url,
		  method: config.method,
		  data: config.method === 'post' ? qs.stringify(config.data) : config.data,
		  header: {
			'appkey': '...',
			'token': '...',
			// 
		  },
		  success: (res) => {
			if (res.statusCode === 200) {
			  resolve(res.data);
			} else {
			  reject(res);
			}
		  },
		  fail: (error) => {
			reject(error);
		  },
		});
	}
  });
}
